.navbar {
    width: 100%;
    height: 70px;
    background: #F7F7F7;
    border-bottom: 1px solid #555;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 80px;
}

.logo-container {
    font-size: 40px;
    margin-left: 100px;
}

.logo-container span {
    font-weight: 500;
    cursor: pointer;
}

.links-container {
    margin-right: 20px;
    display: flex;
    width: 300px;
    justify-content: space-evenly;
}